import axios from 'axios';
import qs from 'qs'

import Auth from './auth.js' 
import router from '../main.js' 
import { Toast,Indicator } from 'mint-ui';
axios.defaults.timeout = 50000;                        //
// axios.defaults.withCredentials = true ;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';           //

axios.defaults.baseURL = 'http://ytapi.hulupi.com/v1';   //
// axios.defaults.baseURL = 'http://www.ytshop.com/v1';   //
// axios.defaults.baseURL = 'http://www.mcyite.com/v1';   //
//POST()

axios.interceptors.request.use((config) => {
    
    //
    if(config.method  === 'post'){
        config.data = qs.stringify(config.data);
    }
    if (Auth.authenticated()) { 
        var token = Auth.getToken(); 
        config.headers.common["Authorization"] = `Bearer ${token}`; 
    } 
    return config;
},(error) =>{
    return Promise.reject(error);
});
axios.interceptors.response.use( 
    response => {
        switch (response.data.code) { 
            case 123456: 
                router.replace({ 
                    path: '/login', 
                    query: {redirect: router.currentRoute.fullPath} 
                }) 
                break;
            case 404: 
                router.replace({ 
                    path: '/NotFound', 
                    query: {redirect: router.currentRoute.fullPath} 
                }) 
                break;
        } 
        return response; 
    }, 
    error => { 
        if (error.response) { 
            switch (error.response.status) { 
                case 500: 
                    Indicator.close();
                    Toast({message:'',position:'top'});
                break; 
                case 404: 
                    router.replace({ 
                        path: '/NotFound', 
                        query: {redirect: router.currentRoute.fullPath} 
                    }) 
                break; 
                case 413: 
                    Indicator.close();
                    Toast({message:'',position:'top'});
                break;       
            } 
        } 
        console.log(error);//console : Error: Request failed with status code 402 
        return Promise.reject(error) 
}); 

